/* Ensure the body and html take up full height */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden; /* Prevent scrolling */
}

#world {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: white;
  position: relative; /* Added for game-over message positioning */
}

.row {
  display: flex;
}

.wall, .coin, .ground, .ghost, .pacman {
  width: 32px;
  height: 32px;
  background-size: cover;
  display: inline-block;
}

.wall {
  background-image: url('./assets/wall.png');
}

.coin {
  background-image: url('./assets/coin.png');
}

.ground {
  background-image: url('./assets/bg.png');
}

.ghost {
  background-image: url('./assets/ghost2.png');
}

.pacman {
  background-image: url('./assets/pacman.png');
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}